import './styles.css'; // Import stylw CSS
import { Notify } from 'notiflix/build/notiflix-notify-aio'; // Import moduu Notify z biblioteki Notiflix
import debounce from 'lodash.debounce'; // Import funkcji debounce z biblioteki lodash
import { fetchCountries } from './fetchCountries'; // Import funkcji fetchCountries z pliku fetchCountries.js

const DEBOUNCE_DELAY = 300; // Opnienie dla funkcji debounce

// Zdefiniowanie odwoa do elementw HTML
const refs = {
  searchEl: document.querySelector('#search-box'), // Pole wyszukiwania
  countryInfo: document.querySelector('.country-info'), // Kontener dla informacji o kraju
  countryList: document.querySelector('.country-list'), // Lista krajw
};

// Funkcja do wyczyszczenia zawartoci elementu HTML
const clearMarkup = ref => (ref.innerHTML = '');

// Obsuga zdarzenia input
const inputHandler = e => {
  const textInput = e.target.value.trim(); // Pobranie wartoci wprowadzonej przez uytkownika

  // Sprawdzenie, czy pole wyszukiwania jest puste
  if (!textInput) {
    clearMarkup(refs.countryList); // Wyczyszczenie listy krajw
    clearMarkup(refs.countryInfo); // Wyczyszczenie informacji o kraju
    return; // Zakoczenie dziaania funkcji
  }

  // Wywoanie funkcji fetchCountries z podanym tekstem
  fetchCountries(textInput)
    .then(data => {
      console.log(data); // Wywietlenie danych zwrconych przez funkcj fetchCountries

      // Sprawdzenie, czy liczba znalezionych krajw jest wiksza ni 10
      if (data.length > 10) {
        clearMarkup(refs.countryList); // Wyczyszczenie listy krajw
        clearMarkup(refs.countryInfo); // Wyczyszczenie informacji o kraju
        Notify.info('Too many matches found. Please enter a more specific name'); // Wywietlenie komunikatu informacyjnego
        return; // Zakoczenie dziaania funkcji
      }

      renderMarkup(data); // Wywoanie funkcji renderMarkup z danymi krajw
    })
    .catch(err => {
      clearMarkup(refs.countryList); // Wyczyszczenie listy krajw
      clearMarkup(refs.countryInfo); // Wyczyszczenie informacji o kraju
      Notify.failure('Oops..., there is no country with that name'); // Wywietlenie komunikatu o bdzie
    });
};

// Funkcja do renderowania listy krajw
const renderMarkup = data => {
  // Sprawdzenie, czy liczba krajw wynosi 1
  if (data.length === 1) {
    clearMarkup(refs.countryList); // Wyczyszczenie listy krajw
    const markupInfo = createInfoMarkup(data); // Wygenerowanie znacznikw HTML dla informacji o kraju
    refs.countryInfo.innerHTML = markupInfo; // Wstawienie znacznikw HTML do kontenera informacji o kraju
  } else {
    clearMarkup(refs.countryInfo); // Wyczyszczenie informacji o kraju
    const markupList = createListMarkup(data); // Wygenerowanie znacznikw HTML dla listy krajw
    refs.countryList.innerHTML = markupList; // Wstawienie znacznikw HTML do listy krajw
  }
};

// Funkcja do generowania znacznikw HTML dla listy krajw
const createListMarkup = data => {
  return data
    .map(
      ({ name, flags }) =>
        `<li><img src="${flags.png}" alt="${name.official}" width="60" height="40">${name.official}</li>`
    )
    .join('');
};

// Funkcja do generowania znacznikw HTML dla informacji o kraju
const createInfoMarkup = data => {
  return data.map(
    ({ name, capital, population, flags, languages }) =>
      `<img src="${flags.png}" alt="${name.official}" width="200" height="100">
      <h1>${name.official}</h1>
      <p>Capital: ${capital}</p>
      <p>Population: ${population}</p>
      <p>Languages: ${Object.values(languages)}</p>`
  );
};

// Dodanie nasuchiwania zdarzenia input z wykorzystaniem funkcji debounce
refs.searchEl.addEventListener('input', debounce(inputHandler, DEBOUNCE_DELAY));